function uploadImg(fileName, changeImage, resChangeImg){
    console.log(fileName);
    console.log(resChangeImg);
    console.log(changeImage);
    // 필요한 인자 1. fileName.files[0]
    if(fileName.files[0]){
        const reader = new FileReader();
        reader.onload = e => {
            // 필요인자 2. changeImage
            changeImage.src = e.target.result;
            // 필요인자 3. this.changeImg
            resChangeImg = e.target.result;

        }
        return reader.readAsDataURL(fileName.files[0]);
    }
}

function deleteImg(){
    return 'data:image/png;base64,';
}

export {uploadImg, deleteImg};